{/* <Router>
<div id="frame">
    <Sidepanel />
    <div className="content">
        <Profile />
        <BaseRouter />
    </div>
</div>
</Router> */}


const mapDispatchToProps = dispatch => {
    return {
        checkAuthenticated: () => dispatch(actions.checkAuthenticated)
    }
}

export default connect(null, mapDispatchToProps)(App)
